.homepage {
  min-height: 100vh;
  height: 100%;
  position: relative;
  background-image: url(../image/icon/wrapper.svg);
  margin-bottom: 124px;
}
.homepage::before {
  z-index: -1;
  position: absolute;
  content: "";
  background: #2fd066;
  opacity: 0.15;
  filter: blur(360px);
  height: 156px;
  bottom: 0;
  left: 0;
  right: 0;
}

.header {
  margin-bottom: 194px;
}
.header.fx {
  padding-top: 52px;
}
.header.fx .header__body {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  padding: 15px 30px;
  background-color: rgba(255, 255, 255, 0.8);
  animation: 0.5s ease-in-out header-fixed;
}
.header.fx .header__burger {
  display: block;
  position: relative;
  width: 30px;
  height: 20px;
  position: relative;
  z-index: 99;
}
.header.fx .header__burger span {
  position: absolute;
  background-color: #2fd066;
  left: 0;
  width: 100%;
  height: 2px;
  top: 9px;
  transition: all 0.3s ease 0s;
}
.header.fx .header__burger::before, .header.fx .header__burger::after {
  content: "";
  background-color: #2fd066;
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  transition: all 0.3s ease 0s;
}
.header.fx .header__burger::before {
  top: 0;
}
.header.fx .header__burger::after {
  bottom: 0;
}
.header.fx .header__burger.active::before {
  transform: rotate(45deg);
  top: 9px;
  background-color: #fff;
}
.header.fx .header__burger.active::after {
  transform: rotate(-45deg);
  bottom: 9px;
  background-color: #fff;
}
.header.fx .header__burger.active span {
  transform: scale(0);
}
.header.fx .header-menu {
  position: fixed;
  top: -100%;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #2fd066;
  transition: all 0.3s ease 0s;
}
.header.fx .header-menu.active {
  padding: 15vh 50px 10px;
  top: 0;
}
.header.fx .header-menu.active .header-menu__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.header.fx .header-menu__list li {
  margin: 0 0 5vh 0;
}
.header.fx .header-menu__list li .link-menu,
.header.fx .header-menu__list li .dropbtn.link-menu {
  color: #fff;
}
.header.fx .header-menu__list li .link-menu:hover,
.header.fx .header-menu__list li .dropbtn.link-menu:hover {
  color: blue;
}
@keyframes header-fixed {
  from {
    margin-top: -20%;
  }
  to {
    margin-top: 0;
  }
}

.header__body {
