@media (max-width: 768px) {
  body.lock {
    overflow: hidden;
  }

  .container {
    max-width: 540px;
    padding: 0 15px;
    margin: 0 auto;
  }

  .header {
    position: fixed;
    left: 0;
    right: 0;
    padding: 30px 15px;
    transition: all 0.4s;
  }
  .header__body {
    padding: 0;
  }
  .header.bg {
    background-color: rgba(255, 255, 255, 0.8);
    z-index: 99;
  }

  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    position: relative;
    z-index: 99;
  }
  .header__burger span {
    position: absolute;
    background-color: #2fd066;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s;
  }
  .header__burger::before, .header__burger::after {
    content: "";
    background-color: #2fd066;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s;
  }
  .header__burger::before {
    top: 0;
  }
  .header__burger::after {
    bottom: 0;
  }
  .header__burger.active::before {
    transform: rotate(45deg);
    top: 9px;
    background-color: #fff;
  }
  .header__burger.active::after {
    transform: rotate(-45deg);
    bottom: 9px;
    background-color: #fff;
  }
  .header__burger.active span {
    transform: scale(0);
  }

  .header-menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: #2fd066;
    transition: all 0.3s ease 0s;
  }
  .header-menu.active {
    padding: 15vh 50px 10px;
    top: 0;
  }
  .header-menu.active .header-menu__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .header-menu__list li {
    margin: 0 0 5vh 0;
  }
  .header-menu__list li .link-menu,
.header-menu__list li .dropbtn.link-menu {
    color: #fff;
  }
  .header-menu__list li .link-menu:hover,
.header-menu__list li .dropbtn.link-menu:hover {
    color: blue;
  }

  .dropdown-content {
    position: unset;
  }

  .dropbtn-arrow svg path {
    stroke: #fff;
  }

  .homepage {
    min-height: unset;
    height: unset;
  }

  .main__content {
    padding-top: 140px;
    gap: unset;
  }

  .about-content-heading {
    grid-column: 1/13;
  }

  .about-content .about-content-item:nth-child(1) {
    padding-left: 15px;
    row-gap: 40px;
  }

  .about-content-text.text-top {
    grid-column: 1/7;
  }
